html {
  min-height: 100vh;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: 'Open Sans', sans-serif;
}

#root {
  min-height: 100vh;
}

input {
  font-family: 'Open Sans', sans-serif;
}

input::-webkit-input-placeholder {
  font-family: 'Open Sans', sans-serif;
}

input::-ms-input-placeholder {
  font-family: 'Open Sans', sans-serif;
}

input::placeholder {
  font-family: 'Open Sans', sans-serif;
}

button {
  font-family: 'Open Sans', sans-serif;
}
.app {
  min-height: 100vh;
}
.home {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.registration-form {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #f7f8fc;
}

.registration-form__steps {
  margin-top: 32px;
}
.logo {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
.progress {
  margin-top: 48px;
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}

.progress__step {
  font-weight: 600;
  font-size: 14px;
  text-align: center;
  color: #999AB0;
}

.progress__bar {
  align-self: center;
  margin-top: 8px;
  max-width: 1208px;
  width: 100%;
  height: 2px;
  display: flex;
  border-radius: 1px;
  background-color: #e5e8ed;
}

.progress__bar-indicator {
  background-color: #4A7AE1;
  border-radius: 1px;
  transition: all 0.2s ease-in-out;
}
.step-one {
  display: flex;
  flex-direction: column;
}

.step-one__form {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
}

.step-one__form > .button {
  margin-top: 64px;
}
.title {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  color: #122244;
}
.sub-title {
  align-self: center;
  max-width: 832px;
  width: 100%;
  margin-top: 16px;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  line-height: 27px;
  font-size: 16px;
  text-align: center;
  color: #122244;
}
.input {
  max-width: 376px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.input__field {
  height: 48px;
  box-sizing: border-box;
  border: 0;
  background: #fff;
  box-shadow: 0px 1px 2px rgba(13, 42, 80, 0.16);
  border-radius: 4px;
  outline: none;
  padding-left: 16px;
  border: 2px solid #fff;
  transition: all 0.2s ease-in-out;
  font-size: 16px;
  color: #122244;
}

.input__field:focus {
  border-color: #4A7AE1;
}

.input__field--error {
  border-color: #EB5757 !important;
}

.input__field::-webkit-input-placeholder {
  font-size: 16px;
  color: #999AB0;
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.input__field::-ms-input-placeholder {
  font-size: 16px;
  color: #999AB0;
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.input__field::placeholder {
  font-size: 16px;
  color: #999AB0;
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.input__field:focus::-webkit-input-placeholder {
  opacity: 0;
}

.input__field:focus::-ms-input-placeholder {
  opacity: 0;
}

.input__field:focus::placeholder {
  opacity: 0;
}

.input__label {
  margin-bottom: 8px;
  font-size: 14px;
  color: #122244;
}

.input__error {
  max-width: 368px;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  padding-left: 8px;
  height: 16px;
  font-size: 12px;
  color: #EB5757;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}

.input__error--active {
  opacity: 1;
}
.button {
  height: 48px;
  max-width: 220px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0;
  border-radius: 24px;
  box-sizing: border-box;
  background-color: #4A7AE1;
  box-shadow: 0px 4px 16px rgba(31, 57, 114, 0.16);
  font-weight: 500;
  font-size: 16px;
  color: #FFF;
  outline: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.button:hover {
  background-color: #6492F4;
}

.button:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.button:focus {
  box-shadow: 0px 8px 20px rgba(31, 57, 114, 0.32);
}

.button:disabled {
  cursor: not-allowed;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  background-color: #999AB0;
}

.button__arrow {
  margin-top: 2px;
  margin-left: 8px;
}
.step-two {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
}

.step-two__pdf-list {
  align-self: center;
  margin-top: 40px;
  display: flex;
}

.step-two__pdf-list > .pdf:not(:last-child) {
  margin-right: 40px;
}

.step-two > .button {
  margin-top: 80px;
  align-self: center;
}

@media (max-width: 413px) {
  
  .step-two__pdf-list {
    flex-direction: column;
  }

  .step-two__pdf-list > .pdf:not(:last-child) {
    margin-right: 0;
  }

  .step-two__pdf-list > .pdf:not(:last-child) {
    margin-bottom: 40px;
  }

}
.pdf {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pdf__container {
  display: flex;
  flex-direction: column;
  width: 168px;
  height: 210px;
  box-sizing: border-box;
  padding: 24px;
  background-color: #fff;
  box-shadow: 0px 1px 2px rgba(13, 42, 80, 0.16);
  border-radius: 4px;
  position: relative;
}

.pdf__img {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
  z-index: 1;
}

.pdf__badge {
  align-self: flex-end;
  background-color: #999AB0;
  border-radius: 8px;
  font-weight: 600;
  font-size: 10px;
  text-transform: uppercase;
  color: #fff;
  padding-left: 10px;
  padding-right: 10px;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.pdf__title {
  margin-top: auto;
  font-weight: 600;
  font-size: 16px;
  color: #122244;
  z-index: 2;
}

.pdf__link {
  margin-top: 16px;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  color: #999AB0;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}

.pdf__link:hover {
  color: #122244;
}

.pdf__link-img {
  margin-right: 8px;
}
.step-three {
  display: flex;
  flex-direction: column;
}
.not-found {
  min-height: 100vh;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.not-found > h1 {
  color: #fff;
  font-size: 60px;
}

.not-found > nav > a {
  color: #fff;
  font-size: 22px;
  transition: all 0.2s ease-in-out;
}

.not-found > nav > a:hover {
  color: #61dafb;
}
